.codes {
  background: rgb(42, 42, 42);
  width: 70%;
  border-radius: 4px;
  border: 1px solid rgb(225, 225, 232);
}
.mask_js {
  position: fixed;
  top: 70px;
  left: 0;
  background: rgba(0, 0, 0, 0.6);
  width: 100%;
  height: calc(100%);
  z-index: 99;
  .code {
    background: rgb(42, 42, 42);
    width: 100%;
    height: 100%;
    border-radius: 4px;
    overflow: auto;
    padding-bottom: 120px;

    // transform:;
    // border: 1px solid rgb(225, 225, 232);
  }
}
.iconStyle {
  font-size: 20px;
  color: #ccc;
  margin-left: 10px;
}
